.node {
    width: 25px;
    height: 25px;
    outline: 1px solid rgb(175, 216, 248);
    display: inline-block;
  }

  .finish-node {
    background-color: red;
  }
  
  .start-node {
    background-color: green;
  }

  .node-visited{
    background-color: orange;
  }

  .node-shortest-path{
    background-color: black;
  }